body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.table-bordered,
.table-bordered > thead > tr > th,
.table-bordered > tbody > tr > th,
.table-bordered > tfoot > tr > th,
.table-bordered > thead > tr > td,
.table-bordered > tbody > tr > td,
.table-bordered > tfoot > tr > td {
  border: 1px solid #999;
}
.react-datepicker {
  font-size: 1em;
}
.react-datepicker__header {
  padding-top: 0.8em;
}
.react-datepicker__month {
  margin: 0.4em 1em;
}
.react-datepicker__day-name,
.react-datepicker__day {
  width: 1.9em;
  line-height: 1.9em;
  margin: 0.166em;
}
.react-datepicker__current-month {
  font-size: 1em;
}
.react-datepicker__navigation {
  top: 1em;
  line-height: 1.7em;
  border: 0.45em solid transparent;
}
.react-datepicker__navigation--previous {
  border-right-color: #ccc;
  left: 1em;
}
.react-datepicker__navigation--next {
  border-left-color: #ccc;
  right: 1em;
}

div.navDetails {
  font-size: 12px;
  color: #737373;
}

label.medQns {
  text-align: left !important;
}

div.panelToolTip {
  font-size: 13px;
  display: inline-block;
  margin-bottom: 0;
  vertical-align: top;
}
table.toothTable {
  width: auto;
  display: inline-table;
  margin-bottom: 0;
  margin-right: 10px;
}
tr {
  text-align: center;
}
table.toothTable > thead > tr > th:not(:first-child) {
  width: 40px;
  text-align: center;
}
th,
td {
  vertical-align: middle !important;
  text-align: center;
}

input.miniBox {
  border: 0;
  width: 100%;
  text-align: center;
  background: transparent;
  letter-spacing: -0.5px;
}
/*td.crownBox {
  background: rgb(253, 255, 150);
}
td.bridgeBox {
  background: rgb(255, 227, 166);
}*/
td.error {
  background: rgb(255, 215, 215);
}
td.implantBox {
  background: rgb(223, 223, 223);
}
td.probingDisabledBox {
  background: #dddddd;
}
td.missingBox {
  background: rgb(176, 28, 185);
}
td.disabledBox {
  background: #000000;
}
td.blackout {
  width: 20px;
  background: #000000;
  border: 0 !important;
}

div.alert {
  width: 'fit-content';
}

table.perioLegendTable > tbody > tr > th,
table.perioLegendTable > tbody > tr > td {
  text-align: left;
}

table.perioLegendTable > tbody > tr > td {
  padding-bottom: 5px;
}

table.legendTable,
table.perio6Table {
  margin-bottom: 0;
}

table.legendTable > tbody > tr:first-child > th,
table.legendTable > tbody > tr:first-child > td {
  border-top: 0;
}

table.legendTable > tbody > tr > td:nth-child(2) {
  width: 30px;
}

table.legendTable > tbody > tr > td:last-child {
  width: auto;
  text-align: left;
}

table.legendTable > tbody > tr > td > ul {
  margin-bottom: 0;
}

table.toothTable > tbody > tr > td.noPadding {
  padding: 0;
}

table.perio6Table > tbody > tr > td {
  padding: 0;
}
table.perio6Table {
  border: 0;
  width: 50px;
}

table.perio6Table > tbody > tr:first-child > td {
  border-top: 0;
}
table.perio6Table > tbody > tr:last-child > td {
  border-bottom: 0;
}
table.perio6Table > tbody > tr > td:first-child {
  border-left: 0;
}
table.perio6Table > tbody > tr > td:last-child {
  border-right: 0;
}

.miniBox::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  font-size: 9px;
}

.miniBox::-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  font-size: 9px;
}

.miniBox::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  font-size: 9px;
}

.miniBox:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  font-size: 9px;
}

.miniBox::-ms-input-placeholder {
  /* Microsoft Edge */
  font-size: 9px;
}

/*
table#sectionETable > tbody > tr > td.implantBox > * {
  display: none;
}*/

tbody > tr > td.disabledBox > *,
tbody > tr > td.missingBox > *,
tbody > tr > td.implantBox > * {
  display: none;
}

.tooltip-inner {
  max-width: 600px;
}

.form-horizontal, .form-group, .row {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

